:root {
  --default-color-link: #000;
  --color-active-link: #00acdc;
  --background-header: #fff;
  --box-shadow: 0 2px 4px 0 #0000000d;
  --main-accent: #060b35;
  --secondary: #616070;
  --button-color: #00acdc;
  --button-color-hover: #fff;
  --border-color: #f3f3f3;
  --column-count: 1;
  --color-title-new: #34568b;
  --card-width: 200px;
}

.header {
  background: var(--background-header);
  box-shadow: var(--box-shadow);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.nav {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  display: flex;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: var(--main-accent);
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.nav__link:hover {
  color: var(--color-active-link);
}

.is-active {
  color: var(--color-active-link);
  position: relative;
}

.is-active:after {
  background: var(--color-active-link);
  content: "";
  border-radius: 8px;
  flex-shrink: 0;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  border: 1px solid var(--border-color);
  background: var(--button-color-hover);
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s ease-in-out;
  display: flex;
}

.card__title {
  color: var(--main-accent);
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__button {
  color: var(--button-color-hover);
  text-align: center;
  text-transform: uppercase;
  background-color: var(--button-color);
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  background-color: var(--button-color-hover);
  color: var(--button-color);
  border-color: var(--button-color);
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: var(--color-title-new);
}

.card:hover .nav__link {
  color: var(--button-color);
}

.card:hover .card__button {
  background-color: var(--button-color-hover);
  color: var(--button-color);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__code {
  color: var(--secondary);
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__count-review {
  justify-content: flex-end;
  align-items: center;
  width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 0;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: var(--secondary);
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: var(--main-accent);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-bottom: 0;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: repeat(var(--column-count), var(--card-width));
  grid-row-gap: 46px;
  justify-content: center;
  column-gap: 48px;
  margin-bottom: 72px;
  padding: 50px 48px 0;
  display: grid;
}

@media (width >= 0) {
  .container {
    --column-count: 1;
  }
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

html {
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

body, p, h2 {
  margin: 0;
}

